const opengpg = require('openpgp');

const opengpgApp = document.getElementById('opengpg');

const store = sessionStorage;

const initCookieJar = () => {
    store.setItem('hello', 'wolrd');
};
initCookieJar();

const createOPDisabler = (p) => {
    opengpgApp.style.position = 'relative';
    opengpgApp.setAttribute('inert', '');

    var ml = document.createElement('div');
    ml.className = 'look for me'
    // add styles to the modal layer
    ml.style.position = 'absolute';
    ml.style.top = '0';
    ml.style.left = '0';
    ml.style.width = '100%';
    ml.style.height = '100%';
    ml.style.backgroundColor = 'rgba(0, 0, 0, 0.5)';
    ml.setAttribute('aria-modal', 'true');
    ml.innerHTML = '<h1 style="margin: 25% 0 0 50%; color: white;">WIP</h1>'
    // append to the parent layer
    p.appendChild(ml);

    // set focus to first focusable element in the modal layer
    var ffe = ml.querySelector("button, [href], input, selct, textarea, [tabindex]:not([tabindex='-1'])");
    if(ffe) {
        ffe.focus();
    }
};

// disable for dev
createOPDisabler(opengpgApp);

const identTxt = document.getElementById('keyIdent');
const passTxt = document.getElementById('keyPasswd');
const genKeyBtn = document.getElementById('genKeyBtn');

identTxt.addEventListener('keyup', (evt) => {
    let iv = evt.currentTarget.value;
    let pv = passTxt.value;

    if(iv.length > 0 && pv.length > 0) genKeyBtn.removeAttribute('disabled');
    else genKeyBtn.setAttribute('disabled', '');
});

passTxt.addEventListener('keyup', (evt) => {
    let iv = identTxt.value;
    let pv = evt.currentTarget.value;

    if(iv.length > 0 && pv.length > 0) genKeyBtn.removeAttribute('disabled');
    else genKeyBtn.setAttribute('disabled', '');
});

genKeyBtn.disabled = true;
genKeyBtn.addEventListener('click', (evt) => {
    opengpg.generateKey({
        userIDs: {ident: identTxt.value.trim()},
        type: 'ecc',
        passphrase: passTxt.value.trim(),
        rsaBits: 4096,
        curve: 'curve25519',
        format: 'binary'
    }).then((keypair) => {
        console.log(keypair);
    });
});